import BaseLayer from './baseLayer'
export default class Point extends BaseLayer {
  static defaultProps = Object.assign(BaseLayer.defaultProps, {
    cfg: {
      visible: true
    },
    source: {
      data: null
    },
    color: {
      field: '#4eb3d3'
    },
    size: {
      field: 12
    },
    shape: {
      field: 'circle'
    },
    style: {
      fontWeight: 400,
      textAnchor: 'center', //  center|left|right|top|bottom|top-left
      textOffset: [0, 0], //  [, ]
      spacing: 2, // 
      padding: [4, 4], //  padding []
      stroke: 'white', // 
      strokeWidth: 2, // 
      opacity: 1.0
    }
  })

  initLayer() {
    const { scene } = this.context
    if (this.layer) scene.removeLayer(this.layer)
    const { options = {} } = this.props
    this.layer = scene.PointLayer(options)
  }
}
